body {
  color: #222222;
}

.container {
  width: 100%;
}
@media (min-width: 520px) {
  .container {
    width: 100%;
  }
}
@media (min-width: 960px) {
  .container {
    max-width: 960px;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    flex-wrap: wrap;
  }
}

header {
  background-color: #3c0000;
  color: #ffffff;
}
header .container {
  justify-content: space-between;
}
header h1 {
  margin-top: 10px;
  margin-bottom: 10px;
}
header h1 img {
  object-fit: contain;
  object-position: left;
  max-height: 50px;
  width: 100%;
}
header .menu {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}
header .menu ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
header .menu ul li {
  border-bottom: solid #ffacff 3px;
}
header .menu ul li a {
  color: #ffffff;
  font-weight: bold;
  padding: 5px;
}

